import React from 'react';
import { Col, Row } from 'antd';
import styled from 'styled-components';
import header from '@images/team/team-hero.svg';
import { device, size } from '@src/breakpoints';
import { ContainerWrapper, HeroDescription, HeroHeader } from '@src/Styles';

export const HeroArt = styled.div`
  text-align: right;
  margin-top: 25px;

  @media ${device.laptop} {
    img {
      width: 75%;
    }
  }

  @media ${device.laptopM} {
    img {
      width: 60%;
    }
  }
`;

export const TextWrapper = styled.div`
  padding: 50px 0;

  @media (max-width: ${size.tablet}) {
    padding: 50px 25px;
  }
`;

class Hero extends React.Component {
  render() {
    return (
      <section
        style={{
          background: '#181E26',
          minHeight: '300px',
          width: '100%'
        }}
      >
        <ContainerWrapper>
          <Row type="flex" className="hero" align="middle">
            <Col xs={24} sm={24} md={14} lg={12} xl={12}>
              <TextWrapper>
                <HeroHeader>The Team</HeroHeader>
                <HeroDescription>
                  Founding members of our team have been managing a 24-hour
                  algorithmic trading group since 2014 and cumulatively have
                  over 30 years of electronic trading experience. We have
                  expertise in market microstructure, order routing, order
                  management and have written directly to exchanges across the
                  globe.
                </HeroDescription>
                <HeroDescription>
                  The rest of the MARKET Protocol team comes from a diverse set
                  of technical backgrounds.
                </HeroDescription>
                <HeroDescription>
                  Together, we quickly realized how immature the crypto
                  exchanges were. This created an opportunity for
                  conceptualization and development of MARKET Protocol which
                  provided us a blank slate to address issues with both the
                  crypto and traditional exchange models.
                </HeroDescription>
              </TextWrapper>
            </Col>
            <Col xs={0} sm={0} md={10} lg={12} xl={12}>
              <HeroArt>
                <img alt="team" src={header} />
              </HeroArt>
            </Col>
          </Row>
        </ContainerWrapper>
      </section>
    );
  }
}
export default Hero;



